export default {
  name: '',
  type: 'funnel',
  data: [
    {
      name: '',
      data: {
        'd-1': 100,
        'd-2': 200,
        'd-3': 500,
        'd-4': 300
      },
      settings: {}
    },
    {
      name: '',
      data: {
        'd-1': 100,
        'd-2': 200,
        'd-3': 500,
        'd-4': 300
      },
      settings: {
        sequence: ['d-1', 'd-2', 'd-3', 'd-4']
      }
    },
    {
      name: '',
      data: {
        'd-1': 0.100,
        'd-2': 0.200,
        'd-3': 0.500,
        'd-4': 0.300
      },
      settings: {
        dataType: 'percent'
      }
    },
    {
      name: '',
      data: {
        'd-1': 100,
        'd-2': 200,
        'd-3': 500,
        'd-4': 300
      },
      settings: {
        ascending: true
      }
    }
  ]
}



